.resources {
    font-size: 1.6em;
    padding-left: 200px;
    img {
        height: 50px;
        vertical-align: middle;
        margin-right: 25px;
    }
    h3 {
      padding-top: 20px;
    }
}

.resources-logos {
    // text-align: center;
    padding-left: 200px;
    img {
        max-width: 40%;
        max-height: 200px;
        margin: 10px;
        vertical-align: top;
    }
    a:hover {
        text-decoration: none;
    }
}

.resources, .resources-logos {
    h3 {
        font-size: 26px;
        font-weight: bold;
        margin: 0 0 5px;
    }
}
