footer {
    background: $color-blue;
    color: white;
    padding: 30px 0;
    margin-top: 100px;

    a {
        color: inherit;
    }
}
footer h2 {
    color: white;
    font-weight: normal;
    margin-top: 0;
}
footer p {
    font-size: 0.9em;
}

.footer-flex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    font-size: 0.7em;
}
.footer-flex i{
    font-size: 1.5em;
}

.footer-flex div {
    margin: 0 20px;
}
