@charset "utf-8"; // Our variables
$base-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
$base-font-size: 16px;
$base-font-weight: 400;
$small-font-size: $base-font-size * 0.875;
$base-line-height: 1.5;
$spacing-unit: 30px;
$text-color: #111;
$background-color: #fdfdfd;
$brand-color: #2a7ae2;
$grey-color: #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark: darken($grey-color, 25%);
$color-gold: #f6bd47;
$color-green: #aed690;
$color-red: #f0856c;
$color-blue: #3db4e5; // Width of the content area
$content-width: 800px;
$on-palm: 600px;
$on-laptop: 800px; // Minima also includes a mixin for defining media queries. // Use media queries like this: // @include media-query($on-palm) { // .wrapper { // padding-right: $spacing-unit / 2; // padding-left: $spacing-unit / 2; // } // } // Import partials from the `minima` theme.
@import "normalize";
@import "header";
@import "colors";
@import "form";
@import "vision";
@import "implement";
@import "resources";
@import "principle";
@import "footer";
@import "button";
@import "speechbubble";
@import "fontello";
@import "youtube";
@import "https://fonts.googleapis.com/css?family=Roboto:300, 400, 500";
@import "mugshots";
@import "blockquote";
@import "timeline";
@import "flexbox";

* {
  box-sizing: border-box;
}
body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 1.5;
  color: #555;
}
h1,
h2,
h3,
strong {
  color: #333;
}
h1,
h2,
h3 {
  font-weight: normal;
}
a {
  color: #e81c4f;
  text-decoration: none;
  transition: 0.5s all;
}
a:hover {
  text-decoration: underline;
}
main {
  margin: 30px 0;
}
.list-unstyled {
  padding: 0;
  margin: 0;
  li {
    list-style-type: none;
  }
}
.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20px;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-left {
  text-align: left;
}
.columns-2 {
  -moz-column-count: 2;
  -webkit-column-count: 2;
  column-count: 2;
  -moz-column-gap: 40px;
  -webkit-column-gap: 40px;
  column-gap: 40px;
  p {
    margin-top: 0;
  }
}
.columns-3 {
  -moz-column-count: 3;
  -webkit-column-count: 3;
  column-count: 3;
  -moz-column-gap: 30px;
  -webkit-column-gap: 30px;
  column-gap: 30px;
  p {
    margin-top: 0;
  }
}
.column-nobreak {
  break-inside: avoid-column;
}

@media only screen and (max-width: 840px) {
  .columns-3 {
    -moz-column-count: 2;
    -webkit-column-count: 2;
    column-count: 2;
  }
}
@media only screen and (max-width: 640px) {
  .columns-2,
  .columns-3 {
    -moz-column-count: 1;
    -webkit-column-count: 1;
    column-count: 1;
  }
}
.images-3 {
  img {
    max-width: 30%;
    margin: 20px 5px;
  }
}

/* spam filtering */
#contact #website{ display:none; }
