.implement {
    margin: 40px 0;
}

.implement-toggle {
    display: flex;
    flex-flow: row;
    padding: 10px;
    margin: 15px 0;
    color: white;
    transition: 0.5s all;
    border-radius: 5px;

    .left {
        width: 60px;
        text-align: left;
        align-items: center;
        display: flex;
        i {
            font-size: 2em;
        }
    }
    .center {
        flex: 1;
    }
    .right {
        width: 45px;
        text-align: right;
        align-items: center;
        display: flex;
        i {
            font-size: 1.5em;
        }
    }

    h3 {
        margin: 0;
        text-transform: uppercase;
        color: white;
    }

    p {
        margin: 0;
        font-size: 0.8em;
    }
}

.implement-toggle:hover {
    cursor: pointer;
    &.fill-gold {
        background-color: darken($color-gold, 20%);
    }
    &.fill-red {
        background-color: darken($color-red, 20%);
    }
    &.fill-green {
        background-color: darken($color-green, 20%);
    }
    &.fill-blue {
        background-color: darken($color-blue, 20%);
    }
}

.info {
    height: auto;
    overflow: auto;
    margin: 0 20px;
    transition: 1s all;
    h3 {
        margin: 20px 0 5px;
    }
}
.info-hidden {
    height: 0;
    overflow: hidden;
}
