.mugs {
  flex-wrap: wrap;
    div {
        text-align: center;
        margin: 0 20px 40px;
        // break-inside: avoid-column;
        height: 360px;
    }

    img {
        display: inline-block;
        border-radius: 50%;
        width: 250px;
        height: 250px;
    }

    h1 {
        font-size: 1.4em;
        font-weight: bold;
        margin: 10px 0 0;
    }

    h2 {
        font-size: 1.2em;
        margin: 0;
    }
}
