

.triangle-right {
    position: relative;
    padding: 15px;
    width: 150px;
    margin: 1em 0 3em;
    color: #fff;
    background: #075698;
    background: linear-gradient(#2e88c4, #075698);
    border-radius: 10px;
}

.triangle-right.top {
    background: linear-gradient(#075698, #2e88c4);
}

.triangle-right.left {
    margin-left: 40px;
    background: #075698;
}

.triangle-right.right {
    margin-right: 40px;
    background: #075698;
}

.triangle-right:after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: 50px;
    border-width: 20px 0 0 20px;
    border-style: solid;
    border-color: #075698 transparent;
    display: block;
    width: 0;
}
.triangle-right.top:after {
    top: -20px;
    right: 50px;
    bottom: auto;
    left: auto;
    border-width: 20px 20px 0 0;
    border-color: transparent #075698;
}
.triangle-right.left:after {
    top: 16px;
    left: -40px;
    bottom: auto;
    border-width: 15px 40px 0 0;
    border-color: transparent #075698;
}
.triangle-right.right:after {
    top: 16px;
    right: -40px;
    bottom: auto;
    left: auto;
    border-width: 15px 0 0 40px;
    border-color: transparent #075698;
}
