#timeline {
  max-width: 300px;
  border-left: 6px solid #3db4e5;
  display: flex;
  flex-direction: column;
  margin: 6px;

  .timeline-bullet {
    width: 20px;
    height: 20px;
    background: white;
    border: 4px solid #3db4e5;
    border-radius: 50%;
    position: relative;
    left: -13px;
    flex-basis: 20px;
    flex-grow: 0;
    flex-shrink: 0;
  }

  .timeline-content {
    flex-grow: 1;
    text-transform: uppercase;
  }

  section {
    display: flex;
    flex-direction: row;
    place-items: center;
    margin: 20px 0;
    font-size: 0.8em;
  }

  h3 {
    padding: 0;
    margin: 0;
    font-weight: bold;
  }
}
