.principle {
    margin: 0 0 40px 0;

    p {
        font-size: 1.2em;
        margin: 0 0 50px;
    }

    figure {
        float: right;
        max-width: 350px;
        img {
            width: 100%;
        }
    }
    figcaption {
        font-style: italic;
        font-size: 0.8em;
    }
    ul {
        list-style-type: none;
        position: relative;
	    margin-left: 1em;
	    padding-left: 0;
    }
    li {
        margin-bottom: 10px;
        font-size: 1.1em;
    }
    li:before {
    	content: "\2022";
    	position: absolute;
    	left: -1em;
    	color: $color-gold;
    	margin-right: 5px;
    }
}

.principle-toggle, .principle-notoggle {
    display: flex;
    flex-flow: row;
    padding: 10px;
    margin: 15px 0;
    color: white;
    transition: 0.5s all;
    border-radius: 5px;

    .left {
        width: 60px;
        text-align: left;
        align-items: center;
        display: flex;
        i {
            font-size: 2em;
        }
    }
    .center {
        flex: 1;
        align-items: center;
        display: flex;
    }
    .right {
        width: 45px;
        text-align: right;
        align-items: center;
        display: flex;
        i {
            font-size: 1.5em;
        }
    }

    h3 {
        margin: 0;
        text-transform: uppercase;
        color: white;
    }

    p {
        margin: 0;
        font-size: 0.8em;
    }
}

.principle-toggle:hover {
    cursor: pointer;
    &.fill-gold {
        background-color: darken($color-gold, 20%);
    }
    &.fill-red {
        background-color: darken($color-red, 20%);
    }
    &.fill-green {
        background-color: darken($color-green, 20%);
    }
    &.fill-blue {
        background-color: darken($color-blue, 20%);
    }
}

.principle-notoggle {
    margin: 50px 0 30px;
    position: relative;
}

.principle-notoggle:after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: 50px;
    border-width: 20px 0 0 20px;
    border-style: solid;
    display: block;
    width: 0;
}
