.principle-connections {
    margin:0 40px;
}
.principle-connections > h3 {
    margin-bottom: 10px;
}

.info {
    height: auto;
    overflow: auto;
    margin: 0 20px;
    transition: 1s all;
    h3 {
        margin: 20px 0 5px;
    }
    &.measure-container {
        .map-embed {
            iframe {
                 width: 100%;
                 height: 100%;
                 min-height: 500px;
                 border: 1px solid #cccccc;
            }
        }
        .map-switcher {
            text-align: center;
        }
        .chart {
            text-align: center;
            margin-top: 40px;
            img {
                max-width: 100%;
            }
        }
    }
}
.info-hidden {
    height: 0;
    overflow: hidden;
}

.measure-verbiage {
    margin-top: 30px;
    img {
        max-width: 100%;
    }
}