form {
  width: 75%;
  display: block;
  margin: 0 auto;
}

.input {
    width: 100%;
    border: none;
    padding: 5px;
    background-color: hsl(35, 24%, 83%);
}
input,
textarea {
  margin-top: 1em;
}

textarea:focus, input:focus{
    outline: none;
}

label {
    font-size: 0.8em;
}

.label-checkbox {
    position: relative;

  margin: .5rem;

  font-family: Arial, sans-serif;
  line-height: 135%;

  cursor: pointer;
}


.checkbox {
  position: relative;
  top: rem(-6);

  margin: 0 1rem 0 0 ;

  cursor: pointer;

  &:before {
        transition: 0.3s all ease-in-out;
		content: "";

		position: absolute;
		left: 0;
		z-index: 1;

		width: 1rem;
		height: 1rem;

		border: 2px solid #b6b6b6;
  }

  &:checked {
    &:before {
        transform: rotate(-45deg);
  		height: .5rem;

  		border-color: $color-red;
  		border-top-style: none;
  		border-right-style: none;
    }
  }

  &:after {
    content: "";

    position: absolute;
    top: rem(-2);
    left: 0;

    width: 1.1rem;
    height: 1.1rem;

    background: #fff;

    cursor: pointer;
  }
}

.form-success {
    margin-top: 0 20px 20px;
    background: #DDEEFF;
    border: 1px solid #ccc;
    padding: 20px;
    width: 75%;
    margin: 0 auto;
    display: none;
}