  font-family: 'fontello';
  src: url('../font/fontello.eot?34680252');
  src: url('../font/fontello.eot?34680252#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?34680252') format('woff2'),
       url('../font/fontello.woff?34680252') format('woff'),
       url('../font/fontello.ttf?34680252') format('truetype'),
       url('../font/fontello.svg?34680252#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
