header {
    margin-bottom: 80px;
}

.header-top {
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-logo {
    display: inline-block;
    font-size: 1.5em;
    .text-blue {
        color: #3db4e5;
    }
    img {
        vertical-align: middle;
        height: 70px;
        padding-right: 20px;
    }
    a {
        text-decoration: none;
        color: inherit;
    }
    a:hover {
        text-decoration: none;
    }
}

.header-nav {
    white-space: nowrap;
    font-size: 0.7em;
    line-height: 1.2em;
    a {
        padding: 0 6px;
        white-space: nowrap;
        text-decoration: none;
        color: #333;
        transition: all 0.5s;
    }
    a:hover {
        text-decoration: underline;
    }
    a.nav-blue {
        color: $color-blue;
    }
}

@media (max-width: 880px) {
    .header-nav {
        position: absolute;
        top: 36px;
        left: 100px;
        white-space: normal;
    }
    .header-logo img {
        vertical-align: top;
    }
}

.header-main {
    height: 500px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center top;
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
        color: white;
        font-size: 4.6em;
        font-weight: normal;
        text-shadow: 1px 1px 3px #666;
    }
    h2 {
        color: white;
        font-size: 2.2em;
        text-shadow: 1px 1px 3px #666;
        text-transform: uppercase;
    }
    h1, h2 {
        margin: 0;
    }

    &.header-image-1 {
        background-image: url(../images/header-1.jpg);
    }
    &.header-image-2 {
        background-image: url(../images/header-2.jpg);
    }
    &.header-image-3 {
        background-image: url(../images/header-3.jpg);
    }
    &.header-image-4 {
        background-image: url(../images/header-4.jpg);
    }
    &.header-image-5 {
        background-image: url(../images/header-5.jpg);
    }
    &.header-image-6 {
        background-image: url(../images/header-6.jpg);
    }
    &.header-image-7 {
        background-image: url(../images/header-7.jpg);
    }
    &.header-image-8 {
        background-image: url(../images/header-8.jpg);
        background-position: center bottom;
    }
}


