.define-vision-flex {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    a {
        text-decoration: none;
        color: inherit;
    }
    a:hover {
        text-decoration: none;
    }

    div {
      width: 150px;
      margin: 20px 20px 0;
      padding: 3px 6px;
      transition: box-shadow ease-in-out 0.5s;
      border-radius: 5px;
    }
    div:hover {
      box-shadow: 0px 0px 6px $color-green;
    }

    img {
        width: 80px;
        height: 80px;
    }

    img.rotate-3 {
        transform: rotate(270deg);
    }
    img.rotate-2 {
        transform: rotate(180deg);
    }
    img.rotate-1 {
        transform: rotate(90deg);
    }

    h3 {
        margin: 0;
        text-transform: uppercase;
    }

    p {
        font-size: 0.7em;
        margin: 0;
    }
}
